<template>

  <view
    style="text-align: center;font-size: 40rpx;height: 100rpx;line-height: 100rpx ;font-weight: bold;position: fixed;top:0;z-index: 9;background-color: white;width: 100%;">
    动态</view>
  <!-- 朋友圈动态列表 -->
  <view class="container">
    <scroll-view class="dynamic-list" scroll-y style="margin-top:100rpx ;">
      <view class="dynamic-item" v-for="(item, index) in dynamicList" :key="index">
        <!-- 用户头像和昵称 -->
        <view class="user-info">
          <image class="avatar" :src="item.avatar" mode="aspectFill"></image>
          <text class="nickname">{{ item.nickname }}</text>
        </view>
        <!-- 动态内容 -->
        <view class="content">
          <text class="text">{{ item.content }}</text>
          <!-- 动态图片 -->
          <view class="image-list" v-if="item.images && item.images.length > 0">
            <image class="image" v-for="(img, imgIndex) in item.images" :key="imgIndex" :src="img" mode="aspectFill">
            </image>
          </view>
        </view>
        <!-- 动态时间和点赞评论 -->
        <view class="footer">
          <text class="time">{{ item.time }}</text>
          <view class="actions">
            <view class="action">
              <uni-icons class="action-icon" :type="item.isLiked?'hand-up-filled':'hand-up'" size="30"
                color="rgb(41, 121, 255)" @click="onLike(item)"></uni-icons>
              <text class="action-content">{{item.likeCount}}</text>
            </view>
            <view class="action">
              <uni-icons class="action-icon" type="chat" size="30" color="rgb(41, 121, 255)"
                @click="onComment"></uni-icons>
              <text class="action-content">{{item.commentCount}}</text>
            </view>
          </view>
        </view>
      </view>
      <view style="height: 200rpx;"></view>
    </scroll-view>
  </view>
  <midButton />
  <tabBar :selectedIndex="1" />
</template>

<script setup>
  import midButton from '/components/midbutton.vue'
  import tabBar from '/components/tabBar.vue'
  import {
    ref
  } from 'vue'
  // 朋友圈数据
  const dynamicList = ref([{
      dynamicID: 1,
      avatar: '/static/dinohead.png',
      nickname: '恐龙苯龙',
      content: '今天天气真好，恐龙想CSltdd！',
      images: [
        '/static/dinoonsea.png',
        '/static/csltdd.jpg',
      ],
      time: '1小时前',
      isLiked: false,
      likeCount: 156,
      commentCount: 45,
      commentList: []
    },
    {
      dynamicID: 2,
      avatar: '/static/lyt3.jpg',
      nickname: '小雅',
      content: '分享一张照片～',
      images: ['/static/lyt4.jpg'],
      time: '2小时前',
      isLiked: false,
      likeCount: 99,
      commentCount: 23,
    },
    {
      dynamicID: 3,
      avatar: '/static/zjy.jpg',
      nickname: '小蓝',
      content: '今天学习了新的编程知识，感觉收获满满！',
      images: [],
      time: '3小时前',
      isLiked: false,
      likeCount: 248,
      commentCount: 32,
    },
  ])

  function onLike(item) {
    item.isLiked = !item.isLiked;
    if (item.isLiked) {
      item.likeCount += 1; // 点赞数 +1
    } else {
      item.likeCount -= 1; // 点赞数 -1
    }
  }

  function onComment() {

  }
</script>

<style lang="scss" scoped>
  .container {
    padding: 20rpx;


    .dynamic-list {
      .dynamic-item {
        margin-bottom: 30rpx;
        padding: 20rpx;
        background-color: #fff;
        border-radius: 10rpx;
        box-shadow: 0 2rpx 10rpx rgba(0, 0, 0, 0.1);

        .user-info {
          display: flex;
          align-items: center;
          margin-bottom: 20rpx;

          .avatar {
            width: 80rpx;
            height: 80rpx;
            border-radius: 50%;
            margin-right: 20rpx;
          }

          .nickname {
            font-size: 32rpx;
            font-weight: bold;
          }
        }

        .content {
          margin-bottom: 20rpx;

          .text {
            font-size: 28rpx;
            line-height: 1.5;
          }

          .image-list {
            display: flex;
            flex-wrap: wrap;
            margin-top: 20rpx;

            .image {
              width: 200rpx;
              height: 200rpx;
              margin-right: 10rpx;
              margin-bottom: 10rpx;
              border-radius: 10rpx;
            }
          }
        }

        .footer {
          display: flex;
          justify-content: space-between;
          align-items: center;
          font-size: 24rpx;
          color: #999;

          .actions {
            display: flex;
            align-items: center;

            .action {
              display: flex;
              align-items: center;
              margin-left: 24rpx;

              .action-icon {
                margin-right: 8rpx;
                color: #333;
              }
            }
          }
        }
      }
    }
  }
</style>